/*********************************************
 * GLOBAL STYLES
 *********************************************/
body {
  background: #fff;
  background-color: #fff;
  font-size: 100%;
}

.App {
  /*font-family: "Source Sans Pro", Helvetica, sans-serif;*/
  font-family: "Gotham Pro", Helvetica, sans-serif;
  font-size: 28px;
  font-weight: 400;
  color: #222;
}

::-moz-selection {
  color: #fff;
  background: #98bdef;
  text-shadow: none;
}

::selection {
  color: #fff;
  background: #98bdef;
  text-shadow: none;
}

::-moz-selection {
  color: #fff;
  background: #98bdef;
  text-shadow: none;
}

.section,
.section > section {
  line-height: 1.3;
  font-weight: inherit;
}

/*********************************************
 * HEADERS
 *********************************************/
.App h1,
.App h2,
.App h3,
.App h4,
.App h5,
.App h6 {
  margin: 0 0 20px 0;
  color: #1a4c91;
  /*font-family: "Source Sans Pro", Helvetica, sans-serif;*/
  font-family: "Gotham Pro", Helvetica, sans-serif;
  font-weight: 500;
  line-height: 1.4;
  letter-spacing: normal;
  text-transform: none;
  text-shadow: none;
  word-wrap: break-word;
}

.App h1 {
  font-size: 1.8em;
  font-weight: 600;
}

.App h2 {
  font-size: 1.6em;
}

.App h3 {
  font-size: 1.4em;
}

.App h4 {
  font-size: 1.3em;
}

.App h1 {
  text-shadow: none;
}

/*********************************************
 * OTHER
 *********************************************/
.App p {
  margin: 0.5em 0;
  line-height: 1.4;
}

/* Ensure certain elements are never larger than the slide itself */
.App img,
.App video,
.App iframe {
  max-width: 100%;
  max-height: 100%;
}

.App strong,
.App b {
  font-weight: bold;
}

.App em {
  font-style: italic;
}

.App ol,
.App dl,
.App ul {
  display: block;
  text-align: left;
  margin: 0 0 0 15px;
}

.App ol li,
.App ul li{
  margin: 0.3em 0;
}

.App ol {
  list-style-type: decimal;
}

.App ul {
  list-style-type: disc;
}

.App ul ul {
  list-style-type: square;
}

.App ul ul ul {
  list-style-type: circle;
}

.App ul ul,
.App ul ol,
.App ol ol,
.App ol ul {
  display: block;
  margin-left: 15px;
}

.App dt {
  font-weight: bold;
}

.App dd {
  margin-left: 20px;
}

.App blockquote {
  display: block;
  position: relative;
  width: 70%;
  margin: 20px auto;
  padding: 5px;
  font-style: italic;
  background: rgba(255, 255, 255, 0.05);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
}

.App blockquote p:first-child,
.App blockquote p:last-child {
  display: inline-block;
}

.App q {
  font-style: italic;
}

.App pre {
  display: block;
  position: relative;
  width: 90%;
  margin: 20px auto;
  text-align: left;
  font-size: 0.55em;
  font-family: monospace;
  line-height: 1.2em;
  word-wrap: break-word;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.3);
}

.App code {
  font-family: monospace;
  text-transform: none;
}

.App pre code {
  display: block;
  padding: 5px;
  overflow: auto;
  max-height: 400px;
  word-wrap: normal;
}

.App table {
  margin: auto;
  border-collapse: collapse;
  border-spacing: 0;
}

.App table th {
  font-weight: bold;
}

.App table th,
.App table td {
  text-align: left;
  padding: 0.2em 0.5em 0.2em 0.5em;
  border-bottom: 1px solid;
}

.App table th[align="center"],
.App table td[align="center"] {
  text-align: center;
}

.App table th[align="right"],
.App table td[align="right"] {
  text-align: right;
}

.App table tbody tr:last-child th,
.App table tbody tr:last-child td {
  border-bottom: none;
}

.App sup {
  vertical-align: super;
  font-size: smaller;
}

.App sub {
  vertical-align: sub;
  font-size: smaller;
}

.App small {
  display: inline-block;
  font-size: 0.6em;
  line-height: 1.2em;
  vertical-align: top;
}

.App small * {
  vertical-align: top;
}

/*********************************************
 * LINKS
 *********************************************/
.App a {
  display: inline-block;
  color: #1a4c91;
  text-decoration: none;
  transition: color .15s ease;
  font-weight: 500;
}

.App a:hover {
  color: #6ca0e8;
  text-shadow: none;
  border: none;
}

.App .roll span:after {
  color: #fff;
  background: #1a53a1;
}

/*********************************************
 * IMAGES
 *********************************************/
.App section img {
  margin: 15px 0px;
  /*background: rgba(255, 255, 255, 0.12);*/
}

.App section img.plain {
  border: 0;
  box-shadow: none;
}

.App a img {
  transition: all .15s linear;
}

.App a:hover img {
  /*background: rgba(255, 255, 255, 0.2);*/
  /*border-color: #2a76dd;*/
  /*box-shadow: 0 0 20px rgba(0, 0, 0, 0.55);*/
}

/*********************************************
 * NAVIGATION CONTROLS
 *********************************************/
.App .controls {
  color: #2a76dd;
}

/*********************************************
 * PROGRESS BAR
 *********************************************/
.App .progress {
  background: rgba(0, 0, 0, 0.2);
  color: #2a76dd;
}

.App .progress span {
  transition: width 800ms cubic-bezier(0.26, 0.86, 0.44, 0.985);
}

/*********************************************
 * PRINT BACKGROUND
 *********************************************/
@media print {
  .backgrounds {
    background-color: #fff;
  }
}

