/* наброски для анимации */
.section {
  display: flex;
  background-color: transparent;
  -webkit-animation: scaleDown 700ms ease both;
          animation: scaleDown 700ms ease both;;
}

.sectionBody {
  margin: auto;
  padding: 0 2em;
  background-color: #fff;
}

.sectionBody .sectionBody {
  padding: 0;
}

.section.active {
  -webkit-animation: scaleDownShow 700ms ease both;
          animation: scaleDownShow 700ms ease both;;
}



@-webkit-keyframes scaleDown {
  from {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    opacity: 0;
    -webkit-transform: scale(.8);
            transform: scale(.8);
  }
}



@keyframes scaleDown {
  from {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    opacity: 0;
    -webkit-transform: scale(.8);
            transform: scale(.8);
  }
}

@-webkit-keyframes scaleDownShow {
  from {
    opacity: 0;
    -webkit-transform: scale(.8);
            transform: scale(.8);
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scaleDownShow {
  from {
    opacity: 0;
    -webkit-transform: scale(.8);
            transform: scale(.8);
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

