.Page2List {

}

.Page2ListItem {
  display: flex;
  align-items: stretch;
  justify-content: space-between;

  text-align: left;
  background-color: #9fdbf2;
  font-weight: 600;
  font-size: 0.9em;
}

.Page2ListItem a {
  color: #037cab;
}
.Page2ListItem a:hover{
  color: red !important;
}

.Page2ListItem:nth-child(2n) {
  background-color: #93e2ff;
}

.Page2ListItem:nth-child(2n) a {
  color: #1a4c91;
}

.Page2ListItem:nth-child(2n) .Page2ListItemLeft {
  background-color: #1a4c91;
}

.Page2ListItemLeft {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4em;
  padding: 1em;
  background-color: #0082b3;
  color: #fff;
  font-size: 1.3em;
  font-weight: 700;
  text-align: center;
}

.Page2ListItemRight {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-grow: 10;
  flex-shrink: 1000;
  padding: 0.5em 1.2em;
}

.Title {
  margin: 0 0 2em;
  display: flex;
  align-items: flex-start;
}

.TitleCount {
  width: 60px;
  margin-right: 24px !important;
  font-size: 3em !important;
  line-height: 1 !important;
  font-weight: 700;
  color: #1a4c91;
}

.App .TitleTitle {
  width: 100%;
  margin: 0;
  padding: 0.5em 1em;
  border-radius: 25px 0 25px 0;
  background-color: #0082b3;
  color: #fff;
  font-weight: 500;
  line-height: 1.3;
  text-align: left;
}

.App .TitleVariant2{
  margin: 0 0 24px;
}
.App .TitleVariant2 .TitleTitle {
  padding: 0;
  background-color: transparent;
  color: #1a4c91;
}

.App .TitleVariant3{
  margin: 0 0 6px;
}

.App .TitleVariant4{
  margin: 0 0 1em;
  line-height: 1.2;
  font-weight: 600;
}
.App .TitleVariant4 .TitleTitle {
  padding: 0;
  background-color: transparent;
  color: #1a4c91;
  text-align: center;
}

.App .TitleVariant5{
  margin: 0 0 1em;
  padding: 0 0 0.2em;
  text-align: center;
}
.App .TitleVariant5 .TitleTitle {
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: #1a4c91;
  text-align: center;
  line-height: 1.2;
  font-weight: 600;
  font-size: 1.6em;
}

.App .TitleVariant6 {
  margin-bottom: 12px;
}
.App .TitleVariant6 .TitleTitle {
  font-size: 1.15em;
}

.App .Table{
  width: 100%;
  font-size: 0.5em;
  background-color: #e6f1fc;
}

.App .Table2{
  font-size: 0.7em;
}

.App .Table3{
  font-size: 0.65em;
}

.App .Table .TableRowTitle td {
  background-color: #86a3cb !important;
  text-align: center;
}

.App .Table .TableRowTitle2 td {
  padding: 12px;
  background-color: #86a3cb !important;
  text-align: center;
  font-size: 1.2em;
  font-weight: 600;
}

.App .Table th,
.App .Table td {
  padding: 3px 12px;
  border-width: 1px;
  border-style: solid;
  border-color: #fff;
  vertical-align: middle;
}

.App .Table th {
  padding: 12px;
  text-align: center;
  background-color: #1a4c91;
  color: #fff;
}

.App .Table td:nth-child(1) {
  text-align: center;
}

.App .Table tr:nth-child(2n) td {
  background-color: #cce2f9;
}


.App .Table .TableItog td{
  padding: 6px 12px;
  font-size: 1.3em !important;
  white-space: nowrap;
  font-weight: bold !important;
  background-color: #830f0f !important;
  color: #fff;
}

.App .Table .TableItog td:nth-child(2) {
  text-align: right;
}

.App .Table .TableItogProject td{
  padding: 6px 12px;
  font-size: 1.1em !important;
  white-space: nowrap;
  font-weight: bold !important;
  background-color: #830f0f !important;
  color: #fff;
}

.App .Table .TableItogProject td:nth-child(2) {
  text-align: right;
}

